import Popup from '../../components/propUp/index.vue'
import '../../common/popup.scss'
export default {
	data() {
		return {
			isDad: true,
			showNumber: '0'
		}
	},
	
	
	components: {
		Popup
	},
	
	methods: {
		// 跳转页面
		jumpPage(num){
			if(num == 1) {
				uni.navigateTo({
					url:'../../pages/Web/index'
				})
			}else {
				uni.navigateTo({
					url:'../../pages/User/aboutUs'
				})
			}
		},
		
		// 身份切换
		switchDad(){
			this.showNumber = '1'
		},
		
		//取消弹窗
		calse() {
			this.showNumber = '0'
		},
		
		//确认
		success() {
			this.showNumber = '0' 
		}
	}
}
